.heart-container {
  transform: rotate(-45deg);
}
.heart {
  background-color: var(--main-brown);
  display: inline-block;
  width: 15px;
  height: 15px;
  position: relative;
  /* top: 0; */
  transform: rotate(-45deg);
  position: fixed;
  /* left: calc(0 - 15px);
  right: 0%; */
  /* margin: -10px auto; */
  -webkit-animation: heartbeat 1.25s infinite;
          animation: heartbeat 1.25s infinite;
}
.heart::before,
.heart::after {
  content: "";
  background-color: var(--main-brown);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  position: absolute;
  -webkit-animation: pulsecolor 1.25s infinite;
          animation: pulsecolor 1.25s infinite;
}
.heart::before {
  top: -7px;
  left: 0;
}
.heart::after {
  top: 0;
  left: 7px;
}
@-webkit-keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(1.25);
    background-color: var(--main-brown);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(1.25);
    background-color: var(--main-brown);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes pulsecolor {
  10% {
    background-color: var(--main-brown);
  }
}
@keyframes pulsecolor {
  10% {
    background-color: var(--main-brown);
  }
}

