/* Base Styles */

.bm-item-list {
  display: flex;
  flex-direction: column;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 10px;
  top: 10px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/* End Base Styles */

.mobileMenu {
  width: 50px;
  height: 50px;
  position: fixed;
  z-index: 9999999999999;
}

/* .bm-menu-wrap {
  width: 80%;
} */

.bm-burger-bars {
  background-color: white;
}

.bm-item-list {
  height: 80%;
  width: 70%;
}

.bm-item-list .columnContainer {
  display: none;
}

.bm-item-list a {
  padding: 20px 0;
  font-size: 1.5em;
}

@media (min-width: 1422px){
  .mobileMenu {
    display: none;
  }
}